module.exports = "<h1 id=project-relative-image-urls-for-bootstrap-vue-custom-components>Project relative image URLs for Bootstrap Vue custom components</h1> <blockquote> <p>vue-loader automatically converts project relative <code>src</code> attributes on <code>&lt;img&gt;</code> tags, but doesn&apos;t automatically for Bootrap-Vue custom components that accept image src url tags.</p> </blockquote> <h2 id=vue-loader-transformtorequire-to-resolve-img-paths>Vue-loader <code>transformToRequire</code> to resolve img paths</h2> <p>To have your project convert these custom component image URLs for you, you will need to customize the <a href=https://vue-loader.vuejs.org/en/options.html#transformtorequire><code>transformToRequire</code></a> <code>option</code> for <code>vue-loader</code> in your webpack config.</p> <p>The default value for <code>transformToRequire</code> is:</p> <pre class=hljs>transformToRequire: {\n  <span class=hljs-string>&apos;img&apos;</span>: <span class=hljs-string>&apos;src&apos;</span>,\n  <span class=hljs-string>&apos;image&apos;</span>: <span class=hljs-string>&apos;xlink:href&apos;</span>\n}\n</pre> <p>To allow Bootstrap-Vue components to use project relative URLs, use the following configuration:</p> <pre class=hljs>transformToRequire: {\n  <span class=hljs-string>&apos;img&apos;</span>: <span class=hljs-string>&apos;src&apos;</span>,\n  <span class=hljs-string>&apos;image&apos;</span>: <span class=hljs-string>&apos;xlink:href&apos;</span>,\n  <span class=hljs-string>&apos;b-img&apos;</span>: <span class=hljs-string>&apos;src&apos;</span>,\n  <span class=hljs-string>&apos;b-img-lazy&apos;</span>: [<span class=hljs-string>&apos;src&apos;</span>, <span class=hljs-string>&apos;blank-src&apos;</span>],\n  <span class=hljs-string>&apos;b-card&apos;</span>: <span class=hljs-string>&apos;img-src&apos;</span>,\n  <span class=hljs-string>&apos;b-card-img&apos;</span>: <span class=hljs-string>&apos;img-src&apos;</span>,\n  <span class=hljs-string>&apos;b-carousel-slide&apos;</span>: <span class=hljs-string>&apos;img-src&apos;</span>,\n  <span class=hljs-string>&apos;b-embed&apos;</span>: <span class=hljs-string>&apos;src&apos;</span>\n}\n</pre> <p>This will allow you to use the following format in your <code>.vue</code> files:</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>b-img</span> <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;~/static/picture.jpg&quot;</span> /&gt;</span>\n\n<span class=hljs-tag>&lt;<span class=hljs-name>b-card-img</span> <span class=hljs-attr>img-src</span>=<span class=hljs-string>&quot;~/static/picture.jpg&quot;</span> /&gt;</span>\n</pre> <h3 id=configuring-transformtorequire-in-nuxt>Configuring <code>transformToRequire</code> in Nuxt</h3> <p>In your <code>nuxt.config.js</code> file, add the following to your build section:</p> <pre class=hljs>build: {   \n  extend (config, ctx) {\n    const vueLoader = config.module.rules.find((rule) =&gt; rule.loader === <span class=hljs-symbol>&apos;vue</span>-loader&apos;)\n    vueLoader.query.transformToRequire = {\n      <span class=hljs-symbol>&apos;img&apos;</span>: <span class=hljs-symbol>&apos;src&apos;</span>,\n      <span class=hljs-symbol>&apos;image&apos;</span>: <span class=hljs-symbol>&apos;xlink</span>:href&apos;,\n      <span class=hljs-symbol>&apos;b</span>-img&apos;: <span class=hljs-symbol>&apos;src&apos;</span>,\n      <span class=hljs-symbol>&apos;b</span>-img-lazy&apos;: [<span class=hljs-symbol>&apos;src&apos;</span>, <span class=hljs-symbol>&apos;blank</span>-src&apos;],\n      <span class=hljs-symbol>&apos;b</span>-card&apos;: <span class=hljs-symbol>&apos;img</span>-src&apos;,\n      <span class=hljs-symbol>&apos;b</span>-card-img&apos;: <span class=hljs-symbol>&apos;img</span>-src&apos;,\n      <span class=hljs-symbol>&apos;b</span>-carousel-slide&apos;: <span class=hljs-symbol>&apos;img</span>-src&apos;,\n      <span class=hljs-symbol>&apos;b</span>-embed&apos;: <span class=hljs-symbol>&apos;src&apos;</span>\n    }\n  }\n}\n</pre><h2 id=using-require-to-resolve-image-paths>Using <code>require</code> to resolve image paths</h2> <p>If you cannot set the <code>transforToRequire</code> in your view-loader config, you can alternatively use the <code>require</code> method:</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>b-img</span> <span class=hljs-attr>:src</span>=<span class=hljs-string>&quot;require(&apos;../static/picture.jpg&apos;)&quot;</span> /&gt;</span>\n\n<span class=hljs-tag>&lt;<span class=hljs-name>b-card-img</span> <span class=hljs-attr>:img-src</span>=<span class=hljs-string>&quot;require(&apos;../static/picture.jpg&apos;)&quot;</span> /&gt;</span>\n</pre> ";

